.app-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #eff6ff 0%, #e0e7ff 100%);
  padding: 2rem;
}

.content-wrapper {
  max-width: 1200px;
  margin: 0 auto;
}

.card {
  background: white;
  border-radius: 1rem;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
  padding: 2rem;
}

.title {
  font-size: 2.5rem;
  font-weight: bold;
  color: #1f2937;
  margin-bottom: 0.5rem;
  text-align: center;
}

.subtitle {
  color: #6b7280;
  text-align: center;
  margin-bottom: 2rem;
}

.upload-section {
  margin-bottom: 2rem;
}

.upload-label {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 12rem;
  border: 2px dashed #a5b4fc;
  border-radius: 0.75rem;
  cursor: pointer;
  background-color: #eef2ff;
  transition: background-color 0.2s;
}

.upload-label:hover {
  background-color: #e0e7ff;
}

.upload-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1.25rem 1.5rem;
}

.upload-icon {
  width: 3rem;
  height: 3rem;
  color: #6366f1;
  margin-bottom: 0.75rem;
}

.upload-text-main {
  margin-bottom: 0.5rem;
  font-size: 0.875rem;
  color: #374151;
}

.font-semibold {
  font-weight: 600;
}

.upload-text-sub {
  font-size: 0.75rem;
  color: #6b7280;
}

.file-input {
  display: none;
}

.file-name {
  margin-top: 0.75rem;
  font-size: 0.875rem;
  color: #374151;
  text-align: center;
}

.error-box {
  margin-bottom: 1.5rem;
  padding: 1rem;
  background-color: #fef2f2;
  border: 1px solid #fecaca;
  border-radius: 0.5rem;
  color: #b91c1c;
}

.processing-box {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}

.spinner {
  width: 2rem;
  height: 2rem;
  color: #4f46e5;
  margin-right: 0.75rem;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.results-section {
  margin-top: 1.5rem;
}

.download-buttons {
  display: flex;
  gap: 1rem;
  justify-content: center;
  margin-bottom: 1.5rem;
  flex-wrap: wrap;
}

.download-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-weight: 600;
  border: none;
  cursor: pointer;
  transition: background-color 0.2s;
  color: white;
}

.odd-btn {
  background-color: #4f46e5;
}

.odd-btn:hover {
  background-color: #4338ca;
}

.even-btn {
  background-color: #9333ea;
}

.even-btn:hover {
  background-color: #7e22ce;
}

.btn-icon {
  width: 1.25rem;
  height: 1.25rem;
}

.pages-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5rem;
}

@media (max-width: 768px) {
  .pages-grid {
    grid-template-columns: 1fr;
  }
}

.pages-column {
  display: flex;
  flex-direction: column;
}

.column-title {
  font-size: 1.25rem;
  font-weight: bold;
  color: #1f2937;
  margin-bottom: 1rem;
}

.pages-list {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  max-height: 24rem;
  overflow-y: auto;
}

.page-item {
  background-color: #f9fafb;
  padding: 0.75rem;
  border-radius: 0.5rem;
}

.page-text {
  font-size: 0.875rem;
  color: #4b5563;
  margin: 0;
}

.crop-btn {
  background-color: #059669;
}

.crop-btn:hover {
  background-color: #047857;
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 1rem;
}

.modal-content {
  background: white;
  border-radius: 1rem;
  padding: 2rem;
  max-width: 500px;
  width: 100%;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
}

.modal-content-large {
  background: white;
  border-radius: 1rem;
  padding: 2rem;
  max-width: 900px;
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
}

.modal-content-crop {
  background: white;
  border-radius: 1rem;
  padding: 2rem;
  max-width: 95vw;
  width: auto;
  max-height: 95vh;
  overflow-y: auto;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
}

.modal-title {
  font-size: 1.5rem;
  font-weight: bold;
  color: #1f2937;
  margin-bottom: 0.5rem;
}

.modal-subtitle {
  color: #6b7280;
  margin-bottom: 1.5rem;
  font-size: 0.875rem;
}

.crop-controls {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.crop-layout {
  display: grid;
  grid-template-columns: 300px 1fr;
  gap: 2rem;
  margin-bottom: 1.5rem;
}

.crop-controls-vertical {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

@media (max-width: 768px) {
  .crop-layout {
    grid-template-columns: 1fr;
  }
}

.crop-item {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.crop-label {
  font-size: 0.875rem;
  font-weight: 600;
  color: #374151;
}

.crop-input {
  padding: 0.5rem;
  border: 2px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 1rem;
  width: 100%;
}

.crop-input:focus {
  outline: none;
  border-color: #4f46e5;
}

.crop-input-small {
  padding: 0.5rem;
  border: 2px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  width: 80px;
}

.crop-input-small:focus {
  outline: none;
  border-color: #4f46e5;
}

.crop-slider {
  width: 100%;
  height: 6px;
  border-radius: 5px;
  background: #d1d5db;
  outline: none;
  -webkit-appearance: none;
}

.crop-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #4f46e5;
  cursor: pointer;
}

.crop-slider::-moz-range-thumb {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #4f46e5;
  cursor: pointer;
  border: none;
}

.crop-unit {
  font-size: 0.875rem;
  color: #6b7280;
  position: absolute;
  margin-top: 2.2rem;
  margin-left: 0.5rem;
  pointer-events: none;
}

.modal-buttons {
  display: flex;
  gap: 1rem;
  justify-content: flex-end;
}

.modal-btn {
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-weight: 600;
  border: none;
  cursor: pointer;
  transition: background-color 0.2s;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.cancel-btn {
  background-color: #e5e7eb;
  color: #374151;
}

.cancel-btn:hover {
  background-color: #d1d5db;
}

.download-modal-btn {
  background-color: #059669;
  color: white;
}

.download-modal-btn:hover {
  background-color: #047857;
}

.preview-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 2px solid #e5e7eb;
  border-radius: 0.5rem;
  padding: 1rem;
  background-color: #f9fafb;
}

.preview-title {
  font-size: 1rem;
  font-weight: 600;
  color: #374151;
  margin-bottom: 1rem;
}

.preview-image {
  max-width: 100%;
  max-height: 500px;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.crop-instructions {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  background-color: #eff6ff;
  border: 1px solid #bfdbfe;
  border-radius: 0.5rem;
  padding: 1rem;
  margin-bottom: 1.5rem;
}

.instruction-icon {
  width: 1.5rem;
  height: 1.5rem;
  color: #3b82f6;
  flex-shrink: 0;
}

.crop-instructions p {
  margin: 0;
  color: #1e40af;
  font-size: 0.875rem;
  margin-bottom: 0.25rem;
}

.crop-instructions p:last-child {
  margin-bottom: 0;
}

.mode-toggle {
  display: flex;
  gap: 1rem;
  margin-bottom: 1.5rem;
  justify-content: center;
}

.mode-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border: 2px solid #d1d5db;
  border-radius: 0.5rem;
  background-color: white;
  color: #6b7280;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.mode-btn:hover {
  border-color: #4f46e5;
  color: #4f46e5;
}

.mode-btn.active {
  border-color: #4f46e5;
  background-color: #4f46e5;
  color: white;
}

.mode-icon {
  width: 1.25rem;
  height: 1.25rem;
}

.canvas-container {
  display: flex;
  justify-content: center;
  margin-bottom: 1.5rem;
  background-color: #f3f4f6;
  border-radius: 0.5rem;
  padding: 1rem;
  overflow: auto;
}

.crop-canvas {
  border: 2px solid #d1d5db;
  border-radius: 0.5rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  background-color: white;
  touch-action: none;
  -webkit-user-select: none;
  user-select: none;
}

.mode-toggle {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
  justify-content: center;
  flex-wrap: wrap;
}

.mode-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border: 2px solid #d1d5db;
  border-radius: 0.5rem;
  background-color: white;
  color: #6b7280;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  touch-action: manipulation;
  -webkit-tap-highlight-color: transparent;
}

@media (max-width: 768px) {
  .mode-btn {
    padding: 0.875rem 1.25rem;
    font-size: 0.875rem;
    min-width: 120px;
  }
  
  .mode-icon {
    width: 1.125rem;
    height: 1.125rem;
  }
  
  .canvas-container {
    padding: 0.5rem;
  }
  
  .modal-content-crop {
    padding: 1rem;
  }
  
  .crop-instructions p {
    font-size: 0.8rem;
  }
}